body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.container{
  margin-top: 5%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.introduction{
  justify-content: left;
}

.part1{
  font-weight: bolder;
  font-size: 70px;
}

.part2{
  font-weight: lighter;
  font-size: 36px;
}

.part1, .part2{
  margin-block: 1%;
}

.part3{
  font-weight: normal;
  font-size: 40px;
  margin-top: 15%;
}
